@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
.dark-theme {
  --BG: hsl(230, 17%, 14%);
  --BGPattern: hsl(232, 19%, 15%);
  --CardBG: hsl(228, 28%, 20%);
  --DText: hsl(228, 34%, 66%);
  --Text: hsl(0, 0%, 100%);
  --Toggle: linear-gradient(to right, hsl(210, 78%, 56%) , hsl(146, 68%, 55%));
}

.light-theme {
  --BG: hsl(0, 0%, 100%);
  --BGPattern: hsl(225, 100%, 98%);
  --CardBG: hsl(227, 47%, 96%);
  --DText: hsl(228, 12%, 44%);
  --Text: hsl(230, 17%, 14%);
  --Toggle: hsl(230, 22%, 74%);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  outline: none;
  font-family: "Inter", sans-serif;
}

body {
  background: var(--BG);
}

.main {
  max-width: 1440px;
  margin: 0 auto;
  display: grid;
  width: 80%;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
}

.main__header {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  margin-top: 30px;
}
.main__header .top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: var(--Text);
}
.main__header .top .right {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.main__header .top .right p {
  font-weight: 700;
  color: var(--Text);
  font-size: 1.3em;
  text-transform: capitalize;
}
.main__header .top .right small {
  font-weight: 700;
  text-transform: capitalize;
  color: var(--DText);
}
.main__header .top .left {
  display: flex;
  flex-direction: row;
  gap: 10px;
  padding: 0.5em 0 5em 0;
  justify-items: center;
}
.main__header .top .left .dark {
  color: var(--Toggle);
  font-weight: 700;
  position: relative;
}
.main__header .top .left input {
  appearance: none;
  width: 50px;
  height: 20px;
  cursor: pointer;
  border-radius: 20px;
  background: var(--Toggle);
}
.main__header .top .left .dot {
  background: var(--CardBG);
  border-radius: 100%;
  height: 18px;
  left: -30px;
  top: 1px;
  position: relative;
  width: 18px;
  transform: translateX(0px);
  transition: transform 0.3s ease-out;
}
.main__header .top .left .dot.toggle {
  transform: translateX(-28px);
  transition: transform 0.3s ease-out;
}

.bg {
  position: absolute;
  top: 0;
  background-color: var(--BGPattern);
  z-index: -110;
  width: 100%;
  height: 350px;
}